<template>
    <v-container>
        <div style="margin-top:20px;">
        <v-card-title style="margin-top: -30px;">
            {{ mainEvent.mainTitle }}
        </v-card-title>
        <v-img src="https://www.era-tehnopolis.ru/upload/iblock/8af/3.jpg"></v-img>
        <v-card-text>
            {{ mainEvent.mainDesc }} <v-btn v-if="!mainEvent.mainDesc">Добавить описание</v-btn>
        </v-card-text>
        <v-expand-transition>
      <div v-show="show">
        <v-expansion-panels hover flat popout accordion multiple>
            <v-expansion-panel
                v-for="event in mainEvent.events" 
                :key="event.id" 
                style="margin-bottom: 5px; margin-top: -10px; padding: 0px;"
            >
            <v-expansion-panel-header>
                {{ new Date(event.timeStart).toLocaleDateString() }} | {{ event.title }}
            </v-expansion-panel-header>
            <v-expansion-panel-content>
                    <v-list-item three-line v-for="subevent in event.subevents" :key="subevent.id">
                        <v-list-item-content>
                        <v-list-item-title>{{ subevent.subTitle }} </v-list-item-title>
                        <v-list-item-subtitle>
                        </v-list-item-subtitle>
                        <v-list-item-subtitle>
                            <v-icon>mdi-clock</v-icon>
                            {{ new Date(subevent.subTimeStart).toLocaleTimeString() }} - 
                            {{ new Date(subevent.subTimeStop).toLocaleTimeString() }}
                        </v-list-item-subtitle>

                        <v-list-item-subtitle>
                            <v-icon>mdi-google-maps</v-icon>
                            {{ event.place }}
                        </v-list-item-subtitle>
                        <v-chip
                            color="green"
                            text-color="white"
                            v-if="subevent.isActive == 1"
                            label
                            >
                            Идет сейчас!
                        </v-chip>
                        
                            <v-chip
                            style="border-radius: 0px; !important"
                                color="red"
                                text-color="white"
                                class="chip"
                                v-else-if="subevent.isActive == 2"
                                label
                                >
                                Уже прошло!
                            </v-chip>
                            <v-chip
                                style="border-radius: 0px; !important"
                                color="blue"
                                text-color="white"
                                class="chip"
                                v-else-if="subevent.isActive == 0"
                                label
                                >
                                Ожидается
                            </v-chip>
                        </v-list-item-content>
                    </v-list-item>
                    <v-card-actions class="justify-center">
                    <!-- <v-btn 
                        :loading="isLoad" 
                        @click="getPlace(mainEvent.id, event.id)"
                        outlined
                        v-if="event.isBig"
                    >Показать место
                    </v-btn> -->
                </v-card-actions>
                <div>
                    <span v-if="event.isBig" v-html="img"></span>
                    <!-- <span v-html="event.img"></span> -->
                </div>
                </v-expansion-panel-content>
            </v-expansion-panel>
        </v-expansion-panels>
        </div>
    </v-expand-transition>
        <v-btn
            outlined
            rounded
            width="100%"
            @click="$router.push({ path: `/guests/${eventId}` , params: { title: mainEvent.mainTitle}})"
        >Список участников</v-btn>
    </div>
    </v-container>

</template>

<script>
import axios from 'axios'
    export default {
        data() {
            return {
                mainEvent: {},
                date: [],
                dateFilt: [],
                show: true,
                eventId: '',
                isLoad: false,
                img: '',
            }
        },

        async mounted() {

            if (!this.$store.state.user || Object.keys(this.$store.state.user).length == 0) {
            this.$fire({
                title: "Уведомление",
                text: "Необходимо авторизоваться!",
                type: "error",
            })
            .then(this.$router.push('/howtouse'));
        }
            
            console.log(this.$route.params.id)
            
            await axios.get(this.$store.state.getUrl + 'api/getSingleEvent/' + this.$route.params.id)
            .then(res => {
                console.log(res.data[0])
                this.mainEvent = res.data[0]
            })

            this.$emit('update:title', await this.mainEvent.mainTitle)

            this.getBlue()
            this.eventId = this.mainEvent.id
        },

        methods: {

            getBlue() {
                this.mainEvent.events.forEach(d => {
                    d.subevents.forEach(d => {
                        if (Number(new Date(d.timeStart)) < Date.now() && Number(new Date(d.timeStop)) > Date.now()) {
                            d.isActive = 1
                        }
                        else if (Number(new Date(d.timeStop)) < Date.now()) {
                            d.isActive = 2;
                        }
                        else if (Number(new Date(d.timeStart)) > Date.now()) {
                            d.isActive = 0;
                        }
                    })
                })
            },

            getWeekDay(date) {
                let days = ['ВС', 'ПН', 'ВТ', 'СР', 'ЧТ', 'ПТ', 'СБ'];
                return days[new Date(date).getDay() + 1];
            },

            // async getPlace(main_id, event_id) {
            //     this.isLoad = true
            //     await this.$store.dispatch('getUsers')
            //     await axios.post(this.$store.state.getUrl + 'api/getPlace/', {
            //         guests: await this.$store.state.getUsers,
            //         user: JSON.parse(localStorage.getItem('user')).id
            //     })
            //     .then(res => {
            //         this.img = res.data
            //         this.isLoad = false
            //         // this.mainEvent[main_id].event[event_id].
            //     })
            // }
        }
    }
</script>

<style scoped>
    .isNow {
        background: #d5e6a2;;
    }

    .v-expansion-panel-content__wrap {
        padding: 0px !important;
    }
</style>